import React, { useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { HeaderColorData, ViewProps } from '../../types';
import './Company.scss';
import globalIcon from '../../assets/Company/global.svg';
import aiIcon from '../../assets/Company/ai.svg';
import safetyIcon from '../../assets/Company/safety.svg';
import airplaneIcon from '../../assets/Company/airplane.svg';

const Company: React.FC<ViewProps> = ({
    changeColorData,
}) => {
    const navigate = useNavigate();
    const [scrollPosition, setScrollPosition] = useState(0);

    useEffect(() => {
        // Set initial header color to white
        const initialColorData: HeaderColorData[] = [{
            top: 0,
            color: false,
        }];
        changeColorData(initialColorData);

        const handleScroll = () => {
            const position = window.pageYOffset;
            setScrollPosition(position);
        };

        window.addEventListener('scroll', handleScroll, { passive: true });
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    useEffect(() => {
        const colorData: HeaderColorData[] = [];

        const section1 = document.getElementById('1');

        if (section1) {
            const curScroll = document.documentElement.scrollTop;
            const top1 = section1.getBoundingClientRect().top;

            colorData.push({
                top: top1 + curScroll - 60,
                color: false,
            });
            colorData.push({
                top: document.body.scrollHeight,
                color: true,
            });

            changeColorData(colorData);
        }
    }, []);

    return (
        <div className="companyContainer">
            <div className="companyContent">
                <section
                    className="heroSection"
                    style={{
                        opacity: Math.max(0, Math.min(1, 1 - (scrollPosition - 600) / 200)),
                        visibility: scrollPosition > 800 && scrollPosition < 600 ? 'hidden' : 'visible',
                    }}
                >
                    <div className="heroBackground" />
                    <div className="heroContent">
                        <div className="heroLeft">
                            <h1>Company</h1>
                            <h2>
                                Autonomous GSE
                                <br />
                                Deployed at Scale
                            </h2>
                            <div className="heroStats">
                                <div className="stat">
                                    <span className="statValue">4</span>
                                    <span className="statLabel">Global Offices</span>
                                </div>
                                <div className="stat">
                                    <span className="statValue">24/7</span>
                                    <span className="statLabel">Operations Capability</span>
                                </div>
                            </div>
                        </div>
                        <div className="heroRight">
                            <p className="lead">
                                AeroVect is the aviation industry&apos;s leading AI‑driven ground support equipment (GSE) automation partner, with full fleets of autonomous, OEM‑agnostic solutions deployed in live operations around the globe.
                            </p>
                            <button
                                type="button"
                                className="ctaButton"
                                onClick={() => navigate('/contact')}
                            >
                                Get in Touch
                            </button>
                        </div>
                    </div>
                </section>

                <section className="contentSection">
                    <div className="contentWrapper">
                        <div className="capabilitiesSection">
                            <div className="sectionHeader">
                                <h3>Global Support, Built for Aviation</h3>
                                <p className="sectionSubtitle">
                                    Headquartered in San Francisco with offices in Atlanta, New York and Frankfurt, we focus on the unique operational demands of airlines and airports.
                                </p>
                            </div>

                            <div className="capabilityGrid">
                                <div className="capabilityBlock">
                                    <div className="capabilityIcon">
                                        <img src={airplaneIcon} alt="Airfield Engineering" />
                                    </div>
                                    <div className="capabilityContent">
                                        <h4>Engineered for the Airfield</h4>
                                        <p>
                                            The AeroVect Driver seamlessly recognizes every type of GSE and navigates complex airside traffic—including active aircraft crossings—to transport cargo and baggage safely across the airfield.
                                        </p>
                                    </div>
                                </div>

                                <div className="capabilityBlock">
                                    <div className="capabilityIcon">
                                        <img src={globalIcon} alt="Global Training" />
                                    </div>
                                    <div className="capabilityContent">
                                        <h4>Trained on Airports Worldwide</h4>
                                        <p>
                                            Powered by the world&apos;s largest proprietary dataset of airport driving behavior—collected at top commercial hubs across North America, Europe and Asia, under every weather and operational condition.
                                        </p>
                                    </div>
                                </div>

                                <div className="capabilityBlock">
                                    <div className="capabilityIcon">
                                        <img src={safetyIcon} alt="Safety First" />
                                    </div>
                                    <div className="capabilityContent">
                                        <h4>Safety First</h4>
                                        <p>
                                            Our autonomous systems are designed with multiple layers of redundancy and safety measures, ensuring reliable operations in the most demanding airport environments.
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </div>
    );
};

export default Company;
